.header{
    width: 100%;
    position: fixed;
    top:0;
    left: 0;
    background-color:  #f9f9f9;
    z-index: 1000; 
}

.nav{
    height: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
}

.nav_logo,
.nav_toggle {
    color: black;
    font-weight: 500;
}

.nav_list {
    display: flex;
    column-gap: 2rem;
}

.nav_link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 16px;
    color: rgb(155, 155, 155);
    font-weight: 500;
    transition: 0.3s;
}

.nav_icon,
.nav_close,
.nav_toggle{
    display: none;
}

.active-link,
.nav_link:hover {
    color:#214b7d;
}

@media screen and (max-width:768px) {
  .header {
    top: initial;
    bottom: 0;
  }

  .nav {
    height: 50px;
  }

  .nav_menu {
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: rgb(255, 255, 255);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0,0,0,0.15);
    border-radius: 1.5rem 1.5rem 0 0 ;
    transition: 0.3s;
  }

  .show-menu {
    bottom: 0;
  }

  .nav_list{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }

  .nav_icon {
    font-size: 1.2rem;
  }

  .nav_close{
    position:absolute;
    right: 1.3rem;
    bottom: 0.5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: black;
  }

  .nav_close:hover {
    color:#214b7d;
  }

  .nav_toggle {
    font-size: 1.1rem;
    cursor: pointer;
  }

  .nav_icon,
  .nav_close,
  .nav_toggle {
    display: block;
  }
}

@media screen and (max-width:350px) {
  .nav_menu {
    padding: 2rem 0.25rem 4rem;
  }

  .nav_list {
    column-gap: 0;
  }
}